import {entityConstants} from "../constants";

const initialState = {
    entity: null,
    basicInfo: null,
    loading: true,
    error: ''
};

export function entity(state = initialState, action){
    switch (action.type) {
      case entityConstants.GET_ENTITY_REQUEST:
            return {
                ...state,
                entity: null,
                loading: true
            };
        case entityConstants.GET_ENTITY_SUCCESS:
            return {
                ...state,
                entity: action.payload,
                loading: false
            };
        case entityConstants.GET_ENTITY_FAILURE:
            return {
                ...state,
                entity: null,
                loading: false
            };
            /////////////////// *GET_BASIC_INFO* /////////////////
        case entityConstants.GET_BASIC_INFO_REQUEST:
          return {
            ...state,
          };
      case entityConstants.GET_BASIC_INFO_SUCCESS:
          return {
            ...state,
            basicInfo: action.payload
          };
      case entityConstants.GET_BASIC_INFO_FAILURE:
          return {
            ...state,
            error: "Can not resolve basic info"
          };
        default:
            return state;
    }
}
