import React from 'react';
import HeaderComponent from "./HeaderComponent";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import {languageActions} from "../../actions/language.actions";


function HeaderContainer({setLanguage, language, basicInfo}) {

  return <HeaderComponent {...{setLanguage, language, basicInfo}}/>
}

const mapStateToProps = ({entity, language}) => ({
  language: language.currentLanguage,
  basicInfo: entity.basicInfo && entity.basicInfo.categoryFields
});

const mapDispatchToProps = dispatch => ({
  ...bindActionCreators(languageActions, dispatch)
});

const connectedHeader = connect(
  mapStateToProps,
  mapDispatchToProps)(HeaderContainer);

export {connectedHeader as Header};
