import {entityServices} from '../services';
import {entityConstants} from "../constants";

export const entityActions = {
    getEntity,
    getBasicInfo
};

function getEntity(entityKey){
    return dispatch => {
        dispatch(request());
        entityServices.getEntity(entityKey)
            .then(entity => dispatch(success(entity)))
            .catch(error => dispatch(failure(error)))
    };

    function request() { return { type: entityConstants.GET_ENTITY_REQUEST } }
    function success(entity) { return { type: entityConstants.GET_ENTITY_SUCCESS, payload: entity } }
    function failure(error) { return { type: entityConstants.GET_ENTITY_FAILURE, error } }
}

function getBasicInfo(){
    return dispatch => {
      dispatch(request());
      entityServices.getBasicInfo()
        .then(res => dispatch(success(res)))
        .catch(error => dispatch(failure(error)))
    };

    function request() { return { type: entityConstants.GET_BASIC_INFO_REQUEST } }
    function success(data) { return { type: entityConstants.GET_BASIC_INFO_SUCCESS, payload: data } }
    function failure(error) { return { type: entityConstants.GET_BASIC_INFO_FAILURE, error } }
}
