import React, {lazy, Suspense} from 'react';
import './homepage.scss';
const Slider = lazy(() => import('./Templates/Slider'));
const Info = lazy(() => import('./Templates/Info'));
const AboutUs = lazy(() => import('./Templates/AboutUs'));
const Products = lazy(() => import('./Templates/Products'));
const Examples = lazy(() => import('./Templates/Examples'));
const Contacts = lazy(() => import('./Templates/Contacts'));


function HomePageComponent({slider, info_1, home_about_us: aboutUs, loading, language}){
    return(
        <div className="homepage-wrapper">
            {
                loading ? "Loading" : (
                    <div>
                      <Suspense fallback={<div>Загрузка...</div>}>
                        <Slider {...{slider, language}} />
                        <Info {...{...info_1, language}} />
                        <AboutUs {...{...aboutUs, language}} />
                        <Products />
                        <Examples />
                        <Contacts />
                      </Suspense>
                    </div>
                )
            }
        </div>
    )
}

export default HomePageComponent;
